ay when it has the .active class */\r\n\r\n.overlay.active {\r\n    display: block;\r\n    opacity: 1;\r\n}\r\n\r\n#dismiss {\r\n    width: 35px;\r\n    height: 35px;\r\n    position: absolute;\r\n    /* top right corner of the sidebar */\r\n    top: 10px;\r\n    right: 10px;\r\n}\r\n\r\n/***************bg light cambio a rojo de bootstrap*******************/\r\n\r\n.bg-light {\r\n    background-color: white !important;\r\n    border-bottom: 1px solid gray;\r\n    padding: 10px;\r\n    box-shadow: 0px 2px #bebaba;\r\n}\r\n\r\n#content>nav>div>ul>a>li>img {\r\n    width: 228px !important;\r\n}\r\n\r\n.title-nav {\r\n    color: white;\r\n    font-weight: bold;\r\n    font-style: normal;\r\n}\r\n\r\n.dropdown-toggle::after {\r\n    margin-left: 1.255em !important;\r\n}\r\n\r\n/*\r\n.container-content {\r\n  padding-top: 30px;\r\n}\r\n*/\r\n\r\n/****************************************** MENU SIDEBAR **************************************************/\r\n\r\n#sidebar>div>ul>li {\r\n    margin-top: 20px;\r\n}\r\n\r\n.item-terms {\r\n    margin-top: 35px !important;\r\n}\r\n\r\n.faqs {\r\n    border-bottom: solid;\r\n    border-color: #80A9A1;\r\n    border-width: 1px;\r\n    margin-top: 35px;\r\n}\r\n\r\n#sidebar>div>div>h3 {\r\n    color: red;\r\n}\r\n\r\n#sidebar>div>ul>li>a {\r\n    color: #929da7;\r\n}\r\n\r\n#pageSubmenu>li>a {\r\n    color: #c7c5c5\r\n}\r\n\r\n#sidebarCollapse {\r\n    width: 40px;\r\n    height: 40px;\r\n    background: white;\r\n}\r\n\r\n#sidebarCollapse span {\r\n    width: 80%;\r\n    height: 2px;\r\n    margin: 0 auto;\r\n    display: block;\r\n    background: gray;\r\n    transition: all 0.8s cubic-bezier(0.810, -0.330, 0.345, 1.375);\r\n}\r\n\r\n#sidebarCollapse span:first-of-type {\r\n    /* rotate first one */\r\n    transform: rotate(45deg) translate(2px, 2px);\r\n}\r\n\r\n#sidebarCollapse span:nth-of-type(2) {\r\n    /* second one is not visible */\r\n    opacity: 0;\r\n}\r\n\r\n#sidebarCollapse span:last-of-type {\r\n    /* rotate third one */\r\n    transform: rotate(-45deg) translate(1px, -1px);\r\n}\r\n\r\n#sidebarCollapse.active span {\r\n    /* no rotation */\r\n    transform: none;\r\n    /* all bars are visible */\r\n    opacity: 1;\r\n    margin: 5px auto;\r\n}\r\n\r\n.wrapper {\r\n    display: flex;\r\n    align-items: stretch;\r\n    perspective: 1500px;\r\n    height: auto;\r\n}\r\n\r\n#sidebar {\r\n    min-width: 250px;\r\n    max-width: 250px;\r\n    color: #fff;\r\n    transition: all 0.6s cubic-bezier(0.945, 0.020, 0.270, 0.665);\r\n    transform-origin: center left;\r\n    /* Set the transformed position of sidebar to center left side. */\r\n    background-color: #4A535C !important;\r\n    border-right: solid;\r\n    border-right-color: gray !important;\r\n    /*z-index: 999; */\r\n    height: auto;\r\n    border-width: 2px;\r\n}\r\n\r\n.sidebar-header {\r\n    margin-bottom: 115px;\r\n    margin-left: 15px;\r\n}\r\n\r\n#sidebar>div>ul>li:hover {\r\n    background-color: gray;\r\n}\r\n\r\nul>li.navbars-nav.navs-link.active {\r\n    background-color: #5f6b77;\r\n    color: white;\r\n    border-top: 1px solid #80A9A1ed;\r\n    border-bottom: 1px solid #80A9A1ed;\r\n}\r\n\r\n.sidebar-links {\r\n    margin-top: 50px;\r\n}\r\n\r\n#sidebar.active {\r\n    margin-left: -250px;\r\n    transform: rotateY(100deg);\r\n    /* Rotate sidebar vertically by 100 degrees. */\r\n}\r\n\r\n.container-logo-red {\r\n    height: 30px;\r\n    margin-bottom: 15px;\r\n    margin-top: 15px;\r\n    width: 228px!important;\r\n}\r\n\r\n#sidebar>div>div.sidebar-header>div>a>img {\r\n    width: 228px;\r\n}\r\n\r\n.home {\r\n    border-bottom: solid;\r\n    border-color: #80A9A1ed;\r\n    border-width: 1px;\r\n    margin-top: 15px;\r\n}\r\n\r\n.recommend {\r\n    margin-top: 25px;\r\n}\r\n\r\n.btn-register,\r\n.btn-login {\r\n    padding-right: 25px;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n.btn-register {\r\n    margin-top: 10px;\r\n}\r\n\r\n.container-profile {\r\n    min-height: 60px;\r\n    /* text-align: center; */\r\n    margin: 0;\r\n    /* border-bottom: red !important; */\r\n    /* border-color: red; */\r\n    /* border-bottom: solid; */\r\n    /* border-width: 1px; */\r\n}\r\n\r\n.btn-xs {\r\n    border-radius: 20px;\r\n    width: 100px;\r\n    height: 28px;\r\n    margin: 0 auto;\r\n    padding: 0;\r\n    display: table-cell;\r\n    vertical-align: middle;\r\n}\r\n\r\n.image-user {\r\n    width: 60%;\r\n}\r\n\r\n#sidebar>div>div.container-profile>div>div {\r\n    color: red;\r\n}\r\n\r\n.profile-text,\r\n.profile-name,\r\n.profile-pts {\r\n    /*font-size: 15px;*/\r\n    margin-left: 20px;\r\n}\r\n\r\n.profile-text {\r\n    color: #A5A5A5 !important;\r\n    font-size: 15px;\r\n    margin-top: 10px;\r\n}\r\n\r\nli.navbars-nav.navs-link>a {\r\n    padding-left: 15px;\r\n}\r\n\r\n#sidebar>div>div.container-profile>div.float-left>img {\r\n    margin-left: 15px;\r\n}\r\n\r\n.img-thumbnail {\r\n    background-color: #C9E2E9;\r\n    border: 3px solid #C9E2E9;\r\n    margin-left: 35px;\r\n    width: 70px;\r\n    margin-right: 15px;\r\n    height: 70px;\r\n}\r\n\r\n.pointer {\r\n    cursor: pointer;\r\n}\r\n\r\n.imageUser {\r\n    margin-right: 15px;\r\n}\r\n\r\n#sidebar>div>ul>li.navbars-nav.navs-link>a>span>i {\r\n    vertical-align: top;\r\n}\r\n\r\n.material-icons {\r\n    font-size: 20px;\r\n}\r\n\r\n@media (max-width: 768px) {\r\n    /* Reversing the behavior of the sidebar:\r\n       it'll be rotated vertically and off canvas by default,\r\n       collapsing in on toggle button click with removal of\r\n       the vertical rotation.   */\r\n    #sidebar {\r\n        margin-left: -250px;\r\n        transform: rotateY(100deg);\r\n    }\r\n    #sidebar.active {\r\n        margin-left: 0;\r\n        transform: none;\r\n    }\r\n    /* Reversing the behavior of the bars:\r\n       Removing the rotation from the first,\r\n       last bars and reappear the second bar on default state,\r\n       and giving them a vertical margin */\r\n    #sidebarCollapse span:first-of-type,\r\n    #sidebarCollapse span:nth-of-type(2),\r\n    #sidebarCollapse span:last-of-type {\r\n        transform: none;\r\n        @media(min-width: 300px) and (max-width: 376px) {\r\n            .big-text {\r\n                font-size: 2rem !important;\r\n            }\r\n            #imagen {\r\n                height: 100px;\r\n            }\r\n            #content>nav>div>ul>a>li>img {\r\n                width: 80%;\r\n                height: 50px;\r\n            }\r\n            #sidebarCollapse {\r\n                width: 60px;\r\n                height: 40px;\r\n            }\r\n            .content-live {\r\n                margin-bottom: 20px;\r\n            }\r\n        }\r\n    }\r\n    /* Removing the vertical margin and make the first and last bars rotate again when the sidebar is open, hiding the second bar */\r\n    #sidebarCollapse.active span {\r\n        margin: 0 auto;\r\n    }\r\n    #sidebarCollapse.active span:first-of-type {\r\n        transform: rotate(45deg) translate(2px, 2px);\r\n    }\r\n    #sidebarCollapse.active span:nth-of-type(2) {\r\n        opacity: 0;\r\n    }\r\n    #sidebarCollapse.active span:last-of-type {\r\n        transform: rotate(-45deg) translate(1px, -1px);\r\n    }\r\n    #sidebarCollapse span:first-of-type,\r\n    #sidebarCollapse span:nth-of-type(2),\r\n    #sidebarCollapse span:last-of-type {\r\n        transform: none;\r\n        opacity: 1;\r\n        margin: 5px auto;\r\n    }\r\n    #sidebarCollapse.active span {\r\n        margin: 0 auto !important;\r\n    }\r\n}\r\n\r\n.nav-item>img {\r\n    width: 98% !important;\r\n}\r\n\r\n@media (max-width: 425px) {\r\n    .logo-principal {\r\n        width: 70%\r\n    }\r\n}\r\n\r\n/* Large devices (desktops, 992px and up) */\r\n\r\n@media (min-width: 992px) {\r\n    /************************sticky menu in web****************************/\r\n    .container-sidebar {\r\n        position: -webkit-sticky !important;\r\n        position: sticky !important;\r\n        top: 30px !important;\r\n    }\r\n}\r\n\r\n/* .image-user{\r\n    margin-left:-83px;\r\n} */\r\n/*# sourceMappingURL=data:application/json;base64, */"